body {
  box-sizing: border-box;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

p {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding-top: 32px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  padding-left: 19px;
}

.card__info {
  margin-left: 16px;
}

.card__title {
  color: #060b35;
  margin: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  font-size: 10px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rating-reviews {
  color: #060b35;
  margin-left: 16px;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: baseline;
  margin: 24px 16px 15px 0;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:not(:hover) {
  transition-duration: .3s;
}

.card__button-buy {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: 0 solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 0 0 16px 16px;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  margin-left: 16px;
}

.stars {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  margin-top: -4px;
  display: block;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
